import { call, put } from "redux-saga/effects";

// íë¡ë¯¸ì¤ë¥¼ ê¸°ë¤ë ¸ë¤ê° ê²°ê³¼ë¥¼ ëì¤í¨ì¹íë ì¬ê°
export const createPromiseSaga = (type, promiseCreator) => {
  const [SUCCESS, ERROR] = [`${type}_SUCCESS`, `${type}_ERROR`];
  return function* saga(action) {
    try {
      // ì¬ì¬ì©ì±ì ìíì¬ promiseCreator ì íë¼ë¯¸í°ì action.payload ê°ì ë£ëë¡ ì¤ì í©ëë¤.
      const payload = yield call(promiseCreator, action.payload);
      yield put({ type: SUCCESS, payload });
    } catch (e) {
      yield put({ type: ERROR, error: true, payload: e });
    }
  };
};

// í¹ì  idì ë°ì´í°ë¥¼ ì¡°ííë ì©ëë¡ ì¬ì©íë ì¬ê°
// APIë¥¼ í¸ì¶ í  ë íë¼ë¯¸í°ë action.payloadë¥¼ ë£ê³ ,
// id ê°ì action.metaë¡ ì¤ì í©ëë¤.
export const createPromiseSagaById = (type, promiseCreator) => {
  const [SUCCESS, ERROR] = [`${type}_SUCCESS`, `${type}_ERROR`];
  return function* saga(action) {
    const id = action.meta;
    try {
      const payload = yield call(promiseCreator, action.payload);
      yield put({ type: SUCCESS, payload, meta: id });
    } catch (e) {
      yield put({ type: ERROR, error: e, meta: id });
    }
  };
};

// ë¦¬ëììì ì¬ì© í  ì ìë ì¬ë¬ ì í¸ í¨ìë¤ìëë¤.
export const reducerUtils = {
  // ì´ê¸° ìí. ì´ê¸° data ê°ì ê¸°ë³¸ì ì¼ë¡ null ì´ì§ë§
  // ë°ê¿ ìë ììµëë¤.
  initial: (initialData = null) => ({
    loading: false,
    data: initialData,
    error: null,
  }),
  // ë¡ë©ì¤ ìí. prevStateì ê²½ì°ì ê¸°ë³¸ê°ì null ì´ì§ë§
  // ë°ë¡ ê°ì ì§ì íë©´ null ë¡ ë°ê¾¸ì§ ìê³  ë¤ë¥¸ ê°ì ì ì§ìí¬ ì ììµëë¤.
  loading: (prevState = null) => ({
    loading: true,
    data: prevState,
    error: null,
  }),
  // ì±ê³µ ìí
  success: (payload) => ({
    loading: false,
    data: payload,
    error: null,
  }),
  // ì¤í¨ ìí
  error: (error) => ({
    loading: false,
    data: null,
    error: error,
  }),
};

// ë¹ëê¸° ê´ë ¨ ì¡ìë¤ì ì²ë¦¬íë ë¦¬ëìë¥¼ ë§ë¤ì´ì¤ëë¤.
// type ì ì¡ìì íì, key ë ìíì key (ì: posts, post) ìëë¤.
export const handleAsyncActions = (type, key, keepData = false) => {
  const [SUCCESS, ERROR] = [`${type}_SUCCESS`, `${type}_ERROR`];
  return (state, action) => {
    switch (action.type) {
      case type:
        return {
          ...state,
          [key]: reducerUtils.loading(keepData ? state[key].data : null),
        };
      case SUCCESS:
        return {
          ...state,
          [key]: reducerUtils.success(action.payload),
        };
      case ERROR:
        return {
          ...state,
          [key]: reducerUtils.error(action.payload),
        };
      default:
        return state;
    }
  };
};

// idë³ë¡ ì²ë¦¬íë ì í¸í¨ì
export const handleAsyncActionsById = (type, key, keepData = false) => {
  const [SUCCESS, ERROR] = [`${type}_SUCCESS`, `${type}_ERROR`];
  return (state, action) => {
    const id = action.meta;
    switch (action.type) {
      case type:
        return {
          ...state,
          [key]: {
            ...state[key],
            [id]: reducerUtils.loading(
              // state[key][id]ê° ë§ë¤ì´ì ¸ìì§ ìì ìë ìì¼ëê¹ ì í¨ì±ì ë¨¼ì  ê²ì¬ í data ì¡°í
              keepData ? state[key][id] && state[key][id].data : null
            ),
          },
        };
      case SUCCESS:
        return {
          ...state,
          [key]: {
            ...state[key],
            [id]: reducerUtils.success(action.payload),
          },
        };
      case ERROR:
        return {
          ...state,
          [key]: {
            ...state[key],
            [id]: reducerUtils.error(action.payload),
          },
        };
      default:
        return state;
    }
  };
};
